<template>
  <div>
    <headerBar :header-data="headerData" />
    <!-- 项目信息 begin -->
    <div class="fixed-project">
      <div class="list">
        <h3>{{ dbxq.lcslmc }}</h3>
        <div class="info">
          <span>
            <i class="iconfont iconlianxiren" />
            {{ dbxq.preUsers }}
          </span>
          <span>
            <i class="iconfont iconshijian" />

            {{ dbxq.showTime }}
          </span>
          <span>
            <i class="iconfont iconpingbiaobaogao" />
            {{ dbxq.rwmc }}
          </span>
        </div>
      </div>
    </div>
    <!-- 项目信息 end -->
    <div class="container-mini">

      <div class="content">
        <div class="common-main box">
          <!-- 通用标题 beign -->
          <div class="title">
            <div class="title-left">
              <h2>选择流程和人员</h2>
            </div>
          </div>
          <!-- 通用标题 end -->
          <van-cell title="项目流程" value="建设工程_公开招标_代理_项目管理1" is-link @click="showXmlcPopup" />
          <van-cell title="部门" value="信息部1" is-link @click="showBmPopup" />
          <van-cell title="项目校验人" value="项目校验人1" is-link @click="showXmjyrPopup" />
          <van-cell title="科长" value="科长1" is-link @click="showKzPopup" />
          <div class="page-list">
            <div class="pagelist-left">是否自审</div>
            <div class="pagelist-right left">是</div>
          </div>
          <div class="page-list">
            <div class="pagelist-left">制作方式</div>
            <div class="pagelist-right left">这里是制作方式</div>
          </div>
        </div>
      </div>

      <div class="content">
        <div class="common-main box">
          <div class="txyj">
            <div class="txyj-tit">
              <span class="text-danger">*</span>
              {{ dbxq.taskCommentLabel }}
              <div v-if="spxz === 1" class="label-text info">同意</div>

              <div v-else class="label-text warn">不同意</div>
              <span class="more" @click="goCyy()">选择常用语
                <i class="iconfont iconarrowright" />

              </span>
            </div>
            <div class="txyj-content">
              <textarea v-model="spyj" :placeholder="'请输入'+dbxq.taskCommentLabel" />
            </div>
          </div>
        </div>
      </div>
      <div class="common-space" />
      <div class="foot-btn fixed">
        <ul>
          <!-- <li><van-button type="primary" plain round  @click="subimitSPYJ()">提交</van-button></li> -->
          <li><van-button type="info" round @click="subimitSPYJ()">提交</van-button></li>
        </ul>
      </div>
    </div>

    <transition name="router-slid" mode="out-in">
      <router-view />
    </transition>

    <!-- 项目流程popup beign -->
    <van-popup
      v-model="show1"
      position="bottom"
      closeable
      round
    >
      <van-picker :columns="columns" />
    </van-popup>
    <!-- 项目流程popup end -->

    <!-- 部门popup beign -->
    <van-popup
      v-model="show2"
      position="bottom"
      closeable
      round
    >
      <van-picker :columns="columns2" />
    </van-popup>
    <!-- 部门popup end -->

    <!-- 项目校验人popup beign -->
    <van-popup
      v-model="show3"
      position="bottom"
      closeable
      round
    >
      <van-picker :columns="columns3" />
    </van-popup>
    <!-- 项目校验人popup end -->

    <!-- 科长popup beign -->
    <van-popup
      v-model="show4"
      position="bottom"
      closeable
      round
    >
      <van-picker :columns="columns4" />
    </van-popup>
    <!-- 科长popup end -->

  </div>
</template>

<script>
import headerBar from '@/components/header/headerBar'
import { mapState, mapMutations } from 'vuex'
import { pageRouter } from '@/common/js/mixin.js'

export default {
  name: 'DbsxSpyj',
  components: {
    headerBar
  },
  mixins: [pageRouter],
  data() {
    return {
      spyj: '',
      dbxq: {},
      headerData: {
        title: '待办审批',
        leftUrl: 'back',
        leftText: ' '
      },
      show1: false,
      show2: false,
      show3: false,
      show4: false,
      columns: ['建设工程_公开招标_代理_项目管理1',
        '建设工程_公开招标_代理_项目管理2',
        '建设工程_公开招标_代理_项目管理3',
        '建设工程_公开招标_代理_项目管理4'],
      columns2: ['信息部1',
        '信息部2',
        '信息部3',
        '信息部4'],
      columns3: ['项目校验人1',
        '项目校验人2',
        '项目校验人3',
        '项目校验人4'],
      columns4: ['科长1',
        '科长2',
        '科长3']
    }
  },
  computed: {
    ...mapState({
      userInfo: state => state.user.userInfo,
      // listItem: state => state.jbrdbsx.listItem,
      txBizid: state => state.jbrdbsx.txBizid,
      spxz: state => state.jbrdbsx.spxz,
      cyy: state => state.jbrdbsx.cyy
    })

  },
  watch: {
    // // 检测搜索条件发生变化，并进行搜索
    cyy(newQuery) {
      // this.search(newQuery)
      this.spyj = newQuery
    }
  },
  created() {
    this.spyj = this.cyy
    // this.SET_DBSX_CYY('')
    this.getDbxq()
  },
  methods: {
    ...mapMutations([
      'jbrdbsx/SET_DBSX_CYY',
      'jbrdbsx/SET_Refresh'
    ]),
    getDbxq() {
      this.$store.dispatch('jbrdbsx/getDbxqAction', {
        bizid: this.txBizid // 单点登录传过来的执行人id
      }).then(res => {
        this.dbxq = res
      }).catch(() => {
      })
    },
    subimitSPYJ() {
      if (this.spxz === 0) {
        this.clickBackSP()
      } else {
        this.clickSP()
      }
    },
    clickSP() {
      if (this.spyj != null && this.spyj.length > 0) {
        this.$toast.success('审批成功！')
        // setTimeout(() => {
        // this.$router.push('/DbsxIndex')
        // this.SET_Refresh('true')
        // }, 2000)

        this.$store.dispatch('jbrdbsx/sendSP', {
          id: this.txBizid,
          taskComment: this.spyj
        }).then(() => {
          this.$toast('审批成功！')
          // setTimeout(() => {
          this.$router.push('/DbsxIndex')
          this.SET_Refresh('true')
          // }, 2000)
        }).catch(error => {
          // this.pageStyles = 3
          this.$toast(error)
        })
      } else {
        this.$toast('请设置审批意见')
      }
    },
    clickBackSP() {
      if (this.spyj != null && this.spyj.length > 0) {
        this.$toast.success('退回成功！')
        // setTimeout(() => {
        // this.$router.push('/DbsxIndex')
        // this.SET_Refresh('true')
        // }, 2000)

        this.$store.dispatch('jbrdbsx/sendBackSP', {
          id: this.txBizid,
          taskComment: this.spyj
        }).then(res => {
          this.$toast('退回成功！')
          // setTimeout(() => {
          //   this.$router.push('/DbsxIndex')
          //   this.SET_Refresh('true')
          // }, 2000)
          this.$router.push('/DbsxIndex')
          this.SET_Refresh('true')
        }).catch(error => {
          // this.loading = false
          // this.pageStyles = 3
          this.$toast(error)
        })
      } else {
        this.$toast('请设置审批意见')
      }
    },
    goCyy() {
      this.$router.push({
        name: 'DbsxCyy' })
    },
    showXmlcPopup() {
      this.show1 = true
    },
    showBmPopup() {
      this.show2 = true
    },
    showXmjyrPopup() {
      this.show3 = true
    },
    showKzPopup() {
      this.show4 = true
    }
    // popupClick() {},
    // showPopUp(type) {
    //   this.$set(this.isPopupShow, type, true)
    // },
    // hidePopUp(type) {
    //   this.$set(this.isPopupShow, type, false)
    // }
  }

}

</script>

<style lang="css" scoped>
  .nav-bar{box-shadow: none;}
  .page-list{padding:0 12px;}
</style>
